<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item class="Breadcrumb">
                    <i class="el-icon-lx-cascades"></i>合同管理
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button
                        type="primary"
                        icon="el-icon-delete"
                        class="handle-del mr10"
                        @click="delAllSelection"
                >批量删除</el-button>
                <el-input v-model="query.agent_name" placeholder="合同编号" class="handle-input mr10"></el-input>
                <el-button type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
            </div>
            <el-table
                    :data="tableData"
                    border
                    :cell-style="{padding:'4px 0'}"
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
                    @selection-change="handleSelectionChange"
                    @filter-change="HandleFilterChange"
            >
                <el-table-column type="selection"  align="center"></el-table-column>
                <el-table-column prop="id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="channel_id"  label="渠道编码" align="center"></el-table-column>
                <el-table-column prop="agent_name"  label="代理商名称" align="center"></el-table-column>
                <el-table-column prop="agent_manager" label="渠道经理" align="center"></el-table-column>
                <el-table-column prop="shop1" label="店铺1"></el-table-column>
                <el-table-column prop="address1" label="网址1"></el-table-column>
                <el-table-column prop="due_time1" label="授权终止日期"></el-table-column>
                <el-table-column prop="shop2" label="店铺2"></el-table-column>
                <el-table-column prop="address2" label="网址2"></el-table-column>
                <el-table-column prop="due_time2" label="授权终止日期"></el-table-column>
                <el-table-column prop="shop3" label="店铺3"></el-table-column>
                <el-table-column prop="address3" label="网址3"></el-table-column>
                <el-table-column prop="due_time3" label="授权终止日期"></el-table-column>
                <el-table-column prop="shop4" label="店铺4"></el-table-column>
                <el-table-column prop="address4" label="网址4"></el-table-column>
                <el-table-column prop="due_time4" label="授权终止日期"></el-table-column>
                <el-table-column prop="shop5" label="店铺5"></el-table-column>
                <el-table-column prop="address5" label="网址5"></el-table-column>
                <el-table-column prop="due_time5" label="授权终止日期"></el-table-column>
                <el-table-column prop="contract_date" label="合同终止日期"></el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button type="text" icon="el-icon-edit" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                        <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                        @size-change="handleSizeChange1"
                        @current-change="handleCurrentChange1"
                        :current-page="currentPage"
                        :page-sizes="[10,20,50,100]"
                        :page-size="pageSize"
                        layout="total, sizes,prev, pager, next,jumper"
                        :total="total1"
                        background
                ></el-pagination>
            </div>
        </div>

        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="渠道编码">
                    <el-input v-model="form.channel_id"></el-input>
                </el-form-item>
                <el-form-item label="代理商名称">
                    <el-input v-model="form.agent_name"></el-input>
                </el-form-item>
                <el-form-item label="渠道经理">
                    <el-input v-model="form.agent_manager"></el-input>
                </el-form-item>
                <el-form-item label="店铺1">
                    <el-input v-model="form.shop1"></el-input>
                </el-form-item>
                <el-form-item label="网址">
                    <el-input v-model="form.address1"></el-input>
                </el-form-item>
                <el-form-item label="到期时间">
                    <el-input v-model="form.due_time1"></el-input>
                </el-form-item>
                <el-form-item label="店铺2">
                    <el-input v-model="form.shop2"></el-input>
                </el-form-item>
                <el-form-item label="网址">
                    <el-input v-model="form.address2"></el-input>
                </el-form-item>
                <el-form-item label="到期时间">
                    <el-input v-model="form.due_time2"></el-input>
                </el-form-item>
                <el-form-item label="店铺3">
                    <el-input v-model="form.shop3"></el-input>
                </el-form-item>
                <el-form-item label="网址">
                    <el-input v-model="form.address3"></el-input>
                </el-form-item>
                <el-form-item label="到期时间">
                    <el-input v-model="form.due_time3"></el-input>
                </el-form-item>
                <el-form-item label="店铺4">
                    <el-input v-model="form.shop4"></el-input>
                </el-form-item>
                <el-form-item label="网址">
                    <el-input v-model="form.address4"></el-input>
                </el-form-item>
                <el-form-item label="到期时间">
                    <el-input v-model="form.due_time4"></el-input>
                </el-form-item>
                <el-form-item label="店铺5">
                    <el-input v-model="form.shop5"></el-input>
                </el-form-item>
                <el-form-item label="网址">
                    <el-input v-model="form.address5"></el-input>
                </el-form-item>
                <el-form-item label="到期时间">
                    <el-input v-model="form.due_time5"></el-input>
                </el-form-item>


            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import  {GetWebsite,SearchWebsite,DeleteWebsite,DeleteAllWebsite,EditWebsite} from './../../server/index'
    export default {
        name: 'website',
        data() {
            return {
                total1: 0,
                currentPage:1,
                pageSize:10,
                query: {
                    agent_name: '',
                },
                tableData:[],
                tableData1: [],
                tableData2:[],
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {},
                idx: -1,
                id: -1
            };
        },
        created() {
            this.GetData();

        },
        methods: {
            // 请求全部的网站授权数据
            async GetData(){
                let username = JSON.parse(localStorage.getItem('AgentUser')).username
                console.log(username)
                let result = await GetWebsite(username);
                if(result.statusCode===200){
                    this.tableData1 = result.data
                    this.tableData2 = this.tableData1
                    this.total1 = result.data.length;
                    this.pushtableData()
                    this.currentPage=1
                }else if(result.statusCode===400){
                    this.$router.push({ path: '/' })
                }
            },



// ---------------------------------------------分页开始-------------------------------------------------------
            handleSizeChange1: function(pageSize) { // 每页条数切换
                this.pageSize = pageSize
                this.handleCurrentChange1(this.currentPage);
            },
            handleCurrentChange1: function(currentPage) {//页码切换
                this.currentPage = currentPage
                this.currentChangePage(this.tableData1,currentPage)

            },
            //分页方法（重点）
            currentChangePage(list,currentPage) {
                let from = (currentPage - 1) * this.pageSize;
                let to = currentPage * this.pageSize;
                this.tableData = [];
                for (; from < to; from++) {
                    if (list[from]) {
                        this.tableData.push(list[from]);
                    }
                }
            },

            // 给tabledata赋值
            pushtableData() {
                let from1 = 0;
                let to1 = 10;
                let list=[];
                this.tableData = [];
                for (; from1 < to1; from1++) {
                    if (this.tableData1[from1]) {
                        this.tableData.push(this.tableData1[from1]);
                    }
                }
            },
// ---------------------------------------------分页结束-------------------------------------------------------



            // 触发搜索按钮
            async handleSearch() {
                if(this.query.agent_name){
                    let result = await SearchWebsite(this.query.agent_name);
                    if(result.statusCode === 200){
                        this.$message.success(result.message);
                        this.tableData1 = result.data
                        this.tableData2 = this.tableData1
                        this.total1 = result.data.length;
                        this.pushtableData()
                        this.currentPage=1
                        console.log(result)
                    }else {
                        this.$message.error(result.message);
                    }
                }else {
                    this.GetData();
                }
            },

            // 多选操作
            handleSelectionChange(val) {
                this.multipleSelection = val;
                console.log(val)
            },

            // 批量删除
            async delAllSelection() {
                this.$confirm('确定要批量删除这些合同嘛?','提示',{
                    type:'warning'
                }).then(async ()=>{
                    const length = this.multipleSelection.length;
                    console.log(this.multipleSelection);
                    let result = await DeleteAllWebsite(this.multipleSelection);
                    console.log(result)
                    if(result.statusCode===200){
                        this.$message.success(result.message)
                        this.GetData();
                    }else{
                        this.$message.error(result.message)
                    }
                    this.multipleSelection = [];
                }).catch(()=>{});

                // const length = this.multipleSelection.length;
                // let str = '';
                // this.$message.error(`删除了${str}`);
                // this.multipleSelection = [];
            },

            // 删除操作
            handleDelete(index, row) {
                console.log(row.agent_name)
                // 二次确认删除
                this.$confirm('确定要删除吗？', '提示', {
                    type: 'warning'
                }).then(async () => {
                    let result = await DeleteWebsite(row.agent_name)
                    if(result.statusCode === 200){
                        this.$message.success(result.message);
                        this.GetData();
                    }else {
                        this.$message.error(result.message);
                    }
                }).catch(() => {});
            },

            // 编辑操作
            handleEdit(index, row) {
                this.idx = index;
                this.form = row;
                this.editVisible = true;
            },

            // 保存编辑
            saveEdit() {
                // 二次确认保存编辑
                this.$confirm('确定要修改吗？', '提示', {
                    type: 'warning'
                }).then(async () => {
                    let result = await EditWebsite(this.form);
                    console.log(result)
                    if(result.statusCode === 200){
                        this.$message.success(result.message);
                        this.GetData();
                        this.editVisible = false;
                    }else {
                        this.$message.error(result.message );
                    }
                }).catch(() => {
                    this.$message.error('编辑失败，请重新再试');
                });
                // this.$message.success(`修改第 ${this.idx + 1} 行成功`);
                // this.$set(this.tableData, this.idx, this.form);
            },


            HandleFilterChange(filters){//筛选条件改变触发此事件

                // console.log(filters);
                if(filters.status[0]){
                    //当filters.status[0]存在时说明点击了筛选条件
                    this.tableData2 = this.tableData2.filter((item)=>{
                        // console.log(item)
                        return item.state===filters.status[0]
                    })
                    // console.log(tabledata2);
                    let tableData3 = this.tableData2;
                    this.tableData2 = this.tableData1
                    this.tableData1 = tableData3
                    this.total1 = this.tableData1.length
                    this.pushtableData()
                    this.currentPage = 1//回到第一页
                }else {
                    //当点击重置或全部时会触发
                    this.currentPage = 1
                    this.GetData();
                }



            }
        }
    }
</script>


<style scoped>
    .Breadcrumb{
        font-size: 18px;
    }
    .handle-box {
        margin-bottom: 20px;
    }
    .handle-select {
        width: 120px;
    }
    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .table {
        width: 100%;
        font-size: 14px;
    }
    .red {
        color: #ff0000;
    }
    .mr10 {
        margin-right: 10px;
    }
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }

    .el-table td{
        padding:0!important;
    }
</style>
